.account-info {
  background: $account-info__background no-repeat left top;
  background-size: 100% 100%;
  width: 23.4vw;
  height: 26.5vw;
  max-width: 125px;
  max-height: 115px;
  position: absolute;
  right: 0;
  margin-right: 0.3vw;
  top: 0;
  
  
  &.account-info--menu-only {
    width: 80px;
    height: 88px;
    background-image: url(../images/account-info__background--idle.svg);
    background-position: left top;
    background-size: 100% 100%;
    
    .account-info__wrapper {
      margin-top: 9px;
      margin-left: 9px;
    }
  }
  
  &.account-info--notification {
    width: 93px;
    height: 85px;
    background-image: url(../images/account-info__background--notification.svg);
    background-position: left top;
    background-size: 100% 100%;
    
    .account-info__wrapper {
      margin-top: 11px;
      margin-left: 27px;
    }
  }
  
  &.account-info--list {
    // to do
  }
  
  &.account-info--notification.account-info--list {
    // to do
  }
  
  a.account-info__wrapper {
    display: block;
    text-shadow: none;
    box-shadow: none;
    width: 54px;
    border-radius: 48px;
    margin-top: 11px;
    margin-left: 27px;
    padding-bottom: 10px;
    color: $white;
    
    &:hover {
      text-shadow: none;
      box-shadow: none;
    }
    
    img { 
      height: 100%;
      width: 100%;
      max-width: 48px;
      border-radius: 90px; 
    }
    
    .account-info__trigger-icon {
      float: right;
      margin-top: -11px;
      padding: 0 3px;
      z-index: 5005;
      position: relative;
    }
  }
  
  &__profile {
    position: absolute;
    right: 10px;
    top: 56px;
    height: 18px;
    width: 18px;
    
    a {
      text-shadow: none;
      box-shadow: none;
      display: block;
      height: 100%;
    }
  }
  
  .account-info__menu {
    margin-top: 15px;
    
    &.account-info__dropdown {
      display: none;
      position: absolute;
      width: 200px;
      margin-left: calc( 65px - 200px);
      margin-right: 15px;
      background-color: adjust-lightness($darkblue, -15);
      margin-top: -5px;
      border-radius: 5px;
      z-index: 5004;
      box-shadow: 0px 3px 3px 2px rgba($black, 0.3);
      border: solid 1px rgba($white, 0.1);
    }
    
    &.account-info__dropdown ul {
      margin: 0;
      list-style: none;
      padding: 5px 15px;
      
      li {
        border-bottom: solid 1px rgba($white, 0.5);
        padding: 6px 0;
        font-size: 1.6rem;
        
        &:first-child {
          padding: 3px 0 6px;
        }
        
        &:last-child {
          border: none;
          padding: 6px 0 3px;
        }
        
       a{
          color: $white;
          text-shadow: none;
          box-shadow: none;
          padding: 6px 0;
         
         &:hover {
           color: $pink;
         }
        }
        
        svg, i {
          margin-right: 15px;
        }
    }
      
     
    }
    
  }
  
  .account-info__notifications {
    height: 21px;
    width: 21px;
    margin-top: -50px;
    margin-left: 7px;
    position: absolute;
    
      a {
        display: block;
      height: 21px;
      width: 21px;
      background: $pink;
      border-radius: 20px;
      text-shadow: none;
      box-shadow: none;
      color: #FFF;
      text-align: center;
      vertical-align: middle;
      font-size: 1.2rem;
      line-height: 20px;
      font-weight: 700;
    }
  }
}

@media (min-width: 768px) {
  .account-info {
    //position: absolute !important;
    position: relative !important;
    //left: 15px !important;
    z-index: 5000;
    
    a.account-info__wrapper {
      width: auto;
      height: auto;
      text-shadow: none;
      box-shadow: none;
      color: $white;
      margin: 0 !important;
      display: flex;
      align-items: center;
      padding:0;
      
      &:hover {
        text-shadow: none;
        box-shadow: none;
        background: rgba($white, 0.1);
        border-top-left-radius: 5rem;
        border-bottom-left-radius: 5rem;
        border-top-right-radius: 0.5rem;
        border-bottom-right-radius: 0.5rem;
        color: $pink;
        
        img {
          border: 2px solid rgba($pink, 1);
          box-shadow: 0px 0px 0px 2px rgba(#FFF,0.1);
          transition: all 0.2s;
        }
      }
      
      img {
        display: block;
        width: 100%;
        max-width: 46px;
        height: auto;
        border: 2px solid rgba($black, 0);
        box-shadow: 0px 0px 0px 2px rgba(#FFF,0.75);
        margin-right: 15px;
      }
      
      .account-info__trigger-icon {
        padding: 0px 5px 0;
        margin-top: -5px;
      }
      
    }
    
    &.account-info--notification {
      width: calc(33vw - 30px);
      height: auto;
      max-width: 300px; 
      //left: 15px !important;
      margin-right: 0;
      background-image: none;
      position: relative;
    }
      
    &.account-info--menu-only {
      position: relative;
    width: calc(33vw - 30px);
    height: auto;
      max-height: none; 
    max-width: 300px; 
    background-image: none;
  }
      
  .account-info__menu {

    border-bottom: solid 1px rgba($white, 0.7);
    
    &.account-info__dropdown {
      position: relative;
      width: auto;
      margin: 0;
      background-color: transparent;
      padding: 15px 0;
      box-shadow: none;
      border: none;
    }
    
    &.account-info__dropdown ul {
      display: block;
      padding: 0;
      margin: 0;
      list-style: none;
    }

  }
    
    
  }
  
  
}

@media (min-width: 992px) {
  .account-info.account-info--notification, .account-info.account-info--menu-only {
    width: calc(25vw - 30px);
    max-width: 270px; 
  }
}

@media (min-width: 1200px) {
  .account-info {
    //position: absolute !important;
    z-index: 5000;
  }
  
  .account-info.account-info--notification {
    position: relative !important;
    //transform: none !important;
    //left: 15px !important;
    //margin-left: -600px !important;
  }
}